<template>
  <div class="cart-table">
    <ul>
      <li class="cart-header">
        <div class="cart-book-image cart-heading-image">
          Book
        </div>
        <div class="cart-book-title cart-heading-title">
          Title
        </div>
        <div class="cart-book-price cart-heading-price">
          Price
        </div>
        <div class="cart-book-quantity cart-heading-quantity">
          Quantity
        </div>
        <div class="subtotal cart-heading-subtotal">
          Total
        </div>
      </li>
      <div class="row-sep"></div>
      <li v-for="item in $store.state.cart.items" :key="item.book.bookId">
        <div class="cart-book-image">
          <img
              :src="require('@/assets/images/books/' + bookImageFileName(item.book.title))"
              width="100px"
              :alt="item.book.title">
        </div>
        <div class="cart-book-title">
          {{ item.book.title }}
        </div>
        <div class="cart-book-price">
          ${{ item.book.price }}
        </div>
        <div class="cart-book-quantity">
            <span class="row-book-quantity">{{ item.quantity }}</span>
            <button class="icon-button inc-button" @click="updateCart(item.book, item.quantity +1)">
              <i class="fas fa-plus-circle"></i>
            </button>
          <button class="icon-button dec-button" @click="updateCart(item.book, item.quantity -1)">
            <i class="fas fa-minus-circle"></i>
          </button>

        </div>
        <div class="subtotal">
          $ {{ item.quantity*item.book.price }}
        </div>
      </li>
      <div class="row-sep"></div>
    </ul>
  </div>
</template>

<script>
export default {
  name: "CartTable",
  props:{

  },
  methods:{
    bookImageFileName: function(bookName) {
      let name = bookName.toLowerCase()
      name = name.replace(",","").replace(":","").replace(/ /g, '-')
      console.log(name)
      return `${name}.gif`
    },
    updateCart:function (book, quantity){
        console.log(quantity)
        //let string = JSON.stringify({book, quantity})
        //this.$store.dispatch("updateCart" ,string)
        this.$store.dispatch("updateCart" ,{book, quantity})
    }
  },
}
</script>

