<template>
  <div class="checkout-page">

    <section v-if="cart.empty">
        The shopping cart is empty, nothing to checkout
    </section>
    <section v-else class="checkout-page-body" >
      <form @submit.prevent="submitOrder">
        <div>
          <label for="name">Name</label>
          <input
            type="text"
            size="20"
            id="name"
            name="name"
            v-model.lazy="$v.name.$model"
          />
        </div>
        <template v-if="$v.name.$error">
          <div class="errorWrapper">
            <span class="error" v-if="!$v.name.required">Name is required</span>
            <span class="error" v-else-if="!$v.name.minLength">
            Name must have at least
            {{ $v.name.$params.minLength.min }} letters.
            </span>
            <span class="error" v-else-if="!$v.name.maxLength">
            Name can have at most
            {{ $v.name.$params.maxLength.max }} letters.
            </span>
            <span class="error" v-else> An unexpected error occurred. </span>
          </div>
        </template>

        <div>
          <label for="phone">Phone</label>
          <input
            class="textField"
            type="text"
            size="20"
            id="phone"
            name="phone"
            v-model.lazy="$v.phone.$model"
          />
        </div>
        <template v-if="$v.phone.$error">
          <div>
            <span class="error" v-if="!$v.name.required">Phone is required</span>
            <span class="error" v-else-if="!$v.phone.phone">
            Please enter a valid phone number
            </span>
            <span class="error" v-else> An unexpected error occurred. </span>
          </div>
        </template>


        <div>
          <label for="email">Email</label>
          <input
              type="text"
              size="20"
              id="email"
              name="email"
              v-model.lazy="$v.email.$model"/>
        </div>
        <template v-if="$v.email.$error">
          <div class="errorWrapper">
            <span class="error" v-if="!$v.email.required">Email is required</span>
            <span class="error" v-else-if="!$v.email.email">
            Please enter a valid  email
           </span>
            <span class="error" v-else> An unexpected error occurred. </span>
          </div>
        </template>

        <div>
          <label for="address">Address</label>
          <input
              type="text"
              size="20"
              id="address"
              name="address"
              v-model.lazy="$v.address.$model"/>
        </div>
        <template v-if="$v.address.$error">
          <div class="errorWrapper">
            <span class="error" v-if="!$v.address.required">Address is required</span>
            <span class="error" v-else-if="!$v.address.minLength">
            Name must have at least
            {{ $v.address.$params.minLength.min }} letters.
          </span>
            <span class="error" v-else-if="!$v.address.maxLength">
            Name can have at most
            {{ $v.address.$params.maxLength.max }} letters.
          </span>
            <span class="error" v-else> An unexpected error occurred. </span>
          </div>
        </template>

        <div>
          <label for="ccNumber">Credit card</label>
          <input type="text"
                 size="20"
                 id="ccNumber"
                 name="ccNumber"
                 v-model.lazy="$v.ccNumber.$model" />
        </div>
        <template v-if="$v.ccNumber.$error">
          <div class="errorWrapper">
            <span class="error" v-if="!$v.ccNumber.required">credit Card Number is required</span>
            <span class="error" v-else-if="!$v.ccNumber.creditCard">
            Please enter a valid  credit card number
            </span>
            <span class="error" v-else> An unexpected error occurred.</span>
          </div>
        </template>

        <div>
          <div>
            <label> Exp Date Month </label>
            <select v-model="ccExpiryMonth">
              <option
                  v-for="(month, index) in months"
                  :key="index"
                  :value="index + 1"
              >
                {{ index+1 }}
              </option>
            </select>
          </div>
          <div>
            <label> Year </label>
            <select>
              <option
                  v-for="(year, index) in 15"
                  :key="year"
                  :value="yearFrom(index)"
              >
                {{ yearFrom(index) }}
              </option>
            </select>
          </div>
        </div>
        <div class="inputSubmit">
          <input
              type="submit"
              name="submit"
              :class="[{'not-active': isClickedPurchase} , 'button']"
              id="completePurchaseButton"
              :disabled="checkoutStatus == 'PENDING'"
              value="Complete Purchase"
          />
          <input type="button" name="clearForm"  value="Clear Form" class="button clearForm" v-on:click="clearForm">
        </div>
        <section class="checkout-page-total" v-show="checkoutStatus == ''">
          Your credit card will be charged
          <span class="subtotal">
            ${{ (cart.subtotal + cart.surcharge/100)  }}
          </span>
          <br>
          ( <span class="subtotal">${{ cart.subtotal}}</span> + <span class="subtotal">${{ cart.surcharge/100 }}</span> shipping )
        </section>
      </form>


      <section v-show="checkoutStatus != ''" class="checkoutStatusBox">
        <div v-if="checkoutStatus == 'ERROR'">
          Error: Please fix the problems above and try again.
        </div>

        <div v-else-if="checkoutStatus == 'PENDING'">Processing...</div>

        <div v-else-if="checkoutStatus == 'OK'">Order placed...</div>

        <div v-else>
          An unexpected error occurred, please try again.
        </div>
      </section>


    </section>
  </div>
</template>

<script>
import {
  required,
  // email,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";

import isCreditCard from "validator/lib/isCreditCard";
import isMobilePhone from "validator/lib/isMobilePhone";
import isEmail from "validator/lib/isEmail";

const phone = value => isMobilePhone(value, "en-US");
const email = value => isEmail(value);
const creditCard = value => isCreditCard(value);


export default {
  data() {
    return {
      name: "",
      address: "",
      phone: "",
      email: "",
      ccNumber: "",
      ccExpiryMonth: new Date().getMonth() + 1,
      ccExpiryYear: new Date().getFullYear(),
      checkoutStatus: "",
      isClickedPurchase:false,
    };
  },
  validations: {
    name: {
      required,
      minLength: minLength(4),
      maxLength: maxLength(45),
    },
    phone:{
      required,
      phone
    },
    email:{
      required,
      email
    },
    ccNumber:{
      required,
      creditCard
    },
    address:{
      required,
      minLength: minLength(4),
      maxLength: maxLength(45),
    }
  },

  computed: {
    cart() {
      return this.$store.state.cart;
    },
    months() {
      return [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ];
    },
  },

  methods: {
    clearForm(){
          this.name =  ""
          this.address = ""
          this.phone = ""
          this.email= ""
          this.ccNumber = ""
          this.ccExpiryMonth =  new Date().getMonth() + 1
          this.ccExpiryYear =  new Date().getFullYear()
    },
    submitOrder() {

      this.$store.commit('CLEAR_ORDER_DETAILS', null)
      this.isClickedPurchase = true
      this.$v.$touch();
      if(this.$v.$error){
        this.checkoutStatus = "ERROR"
        this.isClickedPurchase = false
      }else{
        this.checkoutStatus = "PENDING";
        this.isClickedPurchase = false
        setTimeout(() =>{

          this.$store
              .dispatch('placeOrder', {
                name: this.name,
                address: this.address,
                phone: this.phone,
                email: this.email,
                ccNumber: this.ccNumber,
                ccExpiryMonth: this.ccExpiryMonth,
                ccExpiryYear: this.ccExpiryYear
              })
              .then(() => {
                this.checkoutStatus = 'OK'
                this.$router.push({ name: 'confirmation' })
                this.isClickedPurchase = false
              })
              .catch(reason => {
                this.checkoutStatus = 'SERVER_ERROR'
                console.log('Error placing order', reason)
                this.isClickedPurchase = false
              })
          setTimeout(()=> {
            this.$router.push({ name :"confirmation"})
          }, 1000)
        }, 1000)
      }
    },
    yearFrom(index) {
      return new Date().getFullYear() + index;
    },
  },
};
</script>

