      <div class="welcome-text">Welcome to Our PickPick !</div>
      <div class="login-text">Login or Register</div>
    </div>
    <header>
      <section class="header-logo">
        <div class="header-logo-wrapper">
          <router-link to="/">
            <img
                src="@/assets/images/site/bookstore-logo.png"
                alt="bookstore logo"
                width="150px"
                height="auto"
            />
          </router-link>
        </div>

      </section>
      <section class="header-choice">
        <div class="home-button">
          <i class="fas fa-home fa-lg"></i>
          <button>
            <router-link to="/">Home</router-link>
          </button>
        </div>
        <div class="category-button">
          <div  class="category-button-1"
                v-on:click="openCategories">
            <i class="fas fa-list fa-lg"></i>
            <button>Categories</button>
          </div>
          <header-dropdown-menu
              v-on:hideDropDown="hideDropDown"
              :isShow="this.isOpenCategroies">

          </header-dropdown-menu>
        </div>
      </section>
      <section class="header-search-bar">

        <input type="text" name="search-bar">
        <div class="header-search-wrapper">
          <i class="fas fa-search fa-lg"></i>
