    </header>
  </div>

</template>

<script>
import HeaderDropdownMenu from "@/components/HeaderDropdown";
export default {
  name: "AppHeader",
  components: { HeaderDropdownMenu },
  component: {
    HeaderDropdownMenu,
  },
  data(){
    return {
      isOpenCategroies : false
    }
  },
  methods: {
    openCategories: function(){
      let vm = this;
      this.isOpenCategroies = !this.isOpenCategroies;
      if(this.isOpenCategroies){
        setTimeout(function(){
          vm.isOpenCategroies = false
        },5000)
      }
    },
    hideDropDown:function (name){

      if(name.length > 0){
        this.isOpenCategroies = false;

      }

    }
  }
};
</script>

