<template>
  <div class="category-wrapper">
    <CategoryNav v-on:getSelectedBooksByNav="getBooksByClickingNav"></CategoryNav>
    <div class="category-wrapper-title">
      {{ currentCategory == "SOCIALSCIENCE" ? "SOCIAL SCIENCE" : currentCategory }}
    </div>
    <category-book-list :books="this.$store.state.selectedCategoryBooks"> </category-book-list>
  </div>
</template>

<script>
import CategoryBookList from "@/components/CategoryBookList";
import CategoryNav from "@/components/CategoryNav";


export default {
  name: "category",
  components: {
    CategoryBookList,
    CategoryNav
  },
  data: function () {
    return {
      currentCategory:"",
      categoryNameFromDB:"",

    };
  },
  watch:{
    $route:function(){
      this.categoryNameFromDB = this.$route.params.name
      this.currentCategory = this.categoryNameFromDB.toUpperCase();
      this.callSelectedCategoryBooks()

    }
  },
  methods:{
    callSelectedCategoryBooks:function(){
      var vm = this;
      if(vm.$store.state.categories.length == 0 ){
        this.$store.dispatch("fetchCategories").then(function (){
          var data = vm.$store.state.categories.filter(function(item){
            return item.name == vm.categoryNameFromDB})
          if(data.length == 0){
            vm.$router.push('/404')
            return
          }
          var id = data[0].categoryId
          vm.$store.dispatch("fetchSelectedCategoryBooks", id).catch(function (){
            vm.$router.push('/404')
          });
        });
      }else{
        var data = vm.$store.state.categories.filter(function(item){
          return item.name == vm.categoryNameFromDB})
        if(data.length == 0){
          vm.$router.push('/404')
          return
        }
        var id = data[0].categoryId
        this.$store.dispatch("fetchSelectedCategoryBooks", id).catch(function (){
          vm.$router.push('/404')
        });
      }
    },
    getBooksByClickingNav:function (categoryName){

      this.categoryNameFromDB = categoryName
      this.callSelectedCategoryBooks()
      this.currentCategory = this.categoryNameFromDB.toUpperCase()
    }
  },
  created() {
    const self = this;
    this.$store.dispatch("selectCategory", this.$route.params.name).catch(function(){
      self.$router.push('/404')
    });
  },
  beforeRouteEnter(to,from,next){

    var category = to.params.name.toUpperCase()
    var original =  to.params.name
    next(vm => {
      vm._data.categoryNameFromDB = original
      vm._data.currentCategory = category
      vm.callSelectedCategoryBooks()
    })
  }
};
</script>

<style scoped>
.category-wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
  .category-wrapper-title{
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 26px;
  }
</style>
