<template>
  <div class="booklist-item">
    <div class="booklist-item-img">
      <img
        :src="require('@/assets/images/books/' + this.bookImageFileName(book.title))"
        :alt="book.title">
      <div class="middle" v-if="book.isPublic == true">
        <div class="text">Read More</div>
      </div>
    </div>
    <div class="booklist-item-description">
      <div class="booklist-item-name">
        {{ book.title }}
      </div>
      <div class="booklist-item-author">
        {{ book.author }}
      </div>
      <div class="booklist-item-price">
        $ {{book.price}}
      </div>
      <div class="booklist-item-info">
        <button class="shop-cart-btn btn" v-on:click="addToCart(book)">Add to Cart</button>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "categoryBookListItem",
  props: {
    book: {
      type: Object,
      required: true,
    },
  },
  methods:{
    bookImageFileName: function(bookName) {
      let name = bookName.toLowerCase()
      name = name.replace(",","").replace(":","").replace(/ /g, '-')
      console.log(name)
      return `${name}.gif`
    },
    addToCart:function(book) {
      this.$store.dispatch("addToCart",book);
    }
  }
};
</script>
