<template>
  <nav class="category-nav">
    <ul class="categoryButtons">
      <template v-for="category in $store.state.categories">
        <li
            :key="category.categoryId"
            v-if="$route.params.name === category.name"
            class="button unselected-category-button"
            id="selectedCategoryButton"
            v-on:click="clickCategory"
        >
          {{ category.name == "SocialScience" ? "Social Science" :category.name }}
        </li>
        <li
          :key="category.categoryId"
          v-on:click="clickCategory"
          v-else
          class="button unselected-category-button"

        >
          <router-link :to="'/category/' + category.name">
            {{ category.name == "SocialScience" ? "Social Science" :category.name }}
          </router-link>
        </li>
      </template>

    </ul>

  </nav>
</template>

<script>

export default {
  name: "CategoryNav",
  data:function (){
    return {

    }
  },
  created() {

  },
  methods:{

    clickCategory:function(){
      this.$store.dispatch("selectCategory",this.$route.params.name)
      this.$emit("getSelectedBooksByNav",this.$route.params.name)

    }
  }
};
</script>

