<template>
  <div class="category-button-dropdown" v-show="isShow">
    <ul>
      <li v-for="category in this.$store.state.categories"
          :key="category.categoryId"
          class="dropdown-category"
          v-on:click="dropdownHandler($event)"
      >
        <router-link :to="'/category/' + category.name">
          {{ category.name == "SocialScience" ? "Social Science" : category.name  }}
        </router-link>
      </li>
    </ul>

  </div>

</template>

<script>

export default {
  name: "HeaderDropdownMenu",
  props: {
    isShow: {
      type:Boolean,
      required: true,
    },
  },
  data(){
    return {
      categories:[]
    }
  },
  created() {
  },
  methods:{

    dropdownHandler($event){
      let name = $event.target.innerText.trim();
      this.$store.dispatch("selectCategory",name.replace(" ", ''))
      console.log("selectedCategoryName")
      console.log(this.$store.state.selectedCategoryName)
      this.$emit("hideDropDown",name);
    }
  }

};
</script>

